<template>
	<div class="preferences">
		<button class="button theme-changer" @click="changeTheme(theme)"></button>
	</div>
</template>

<script>
import { ref } from "vue"

const themes = {
	dark: {
		"--background-color": "#444444",
		"--text-color": "#fff",
		"--button-box-shadow-outset":
			"5px 5px 10px #3a3a3a, -5px -5px 10px #4e4e4e",
		"--button-box-shadow-outset-pressed":
			"1px 1px 5px #3a3a3a, -1px -1px 5px #4e4e4e",
		"--button-box-shadow-inset":
			"inset 5px 5px 10px #3a3a3a, inset -5px -5px 10px #4e4e4e",
	},
	light: {
		"--background-color": "#eeeeff",
		"--text-color": "#666",
		"--button-box-shadow-outset":
			"5px 5px 10px #a7a7b3, -5px -5px 10px #ffffff",
		"--button-box-shadow-outset-pressed":
			"1px 1px 5px #a7a7b3, -1px -1px 5px #ffffff",
		"--button-box-shadow-inset":
			"inset 5px 5px 10px #a7a7b3, inset -5px -5px 10px #ffffff",
	},
}

export default {
	setup() {
		const theme = ref("light")

		const changeTheme = (lastTheme) => {
			theme.value = lastTheme === "dark" ? "light" : "dark"
      let newTheme = themes[theme.value]
      for(let key in newTheme) {
        document.documentElement.style.setProperty(key, newTheme[key])
      }
		}
		return {
			changeTheme,
			theme,
		}
	},
}
</script>

<style scoped src="./ThePreferences.css">
</style>