<template>
	<div class="calculator">
		<ThePreferences />
		<div class="head">
			<button class="button decoration left" disabled></button>
			<div class="logo">
				made by
				<a href="https://github.com/whenitapproaches">whenitapproaches</a>
			</div>
			<button class="button decoration right" disabled></button>
		</div>
		<div class="display">{{ display }}</div>
		<div>
			<button class="button" @click="pressPercent()">%</button>
			<button class="button" @click="invertNumber()">+/-</button>
			<button class="button" @click="pressReset()">C</button>
			<button class="button is-primary" @click="updateCurrentOperator('/')">
				/
			</button>
		</div>
		<div>
			<button class="button" @click="pressNumber(7)">7</button>
			<button class="button" @click="pressNumber(8)">8</button>
			<button class="button" @click="pressNumber(9)">9</button>
			<button class="button is-primary" @click="updateCurrentOperator('×')">
				×
			</button>
		</div>
		<div>
			<button class="button" @click="pressNumber(4)">4</button>
			<button class="button" @click="pressNumber(5)">5</button>
			<button class="button" @click="pressNumber(6)">6</button>
			<button class="button is-primary" @click="updateCurrentOperator('-')">
				-
			</button>
		</div>
		<div>
			<button class="button" @click="pressNumber(1)">1</button>
			<button class="button" @click="pressNumber(2)">2</button>
			<button class="button" @click="pressNumber(3)">3</button>
			<button class="button is-primary" @click="updateCurrentOperator('+')">
				+
			</button>
		</div>
		<div>
			<button class="button" @click="pressNumber(0)">0</button>
			<button class="button" @click="pressDot()">.</button>
			<button class="button is-2 is-primary" @click="pressEqual()">=</button>
		</div>
	</div>
</template>

<script>
import { useStore } from "@/store"
import { computed } from "vue"
import ThePreferences from '@/components/ThePreferences.vue';
export default {
	components: {
		ThePreferences
	},
	setup() {
		const store = useStore()

		const display = computed(() => store.getters.display)

		const updateCurrentOperator = (operator) => {
			store.dispatch("updateCurrentOperator", operator)
		}

		const pressNumber = (number) => {
			store.dispatch("pressNumber", number)
		}

		const pressEqual = () => {
			store.dispatch("pressEqual")
		}

		const pressReset = () => {
			store.dispatch("pressReset")
		}

		const pressDot = () => {
			store.dispatch("pressDot")
		}

		const invertNumber = () => {
			store.dispatch("invertNumber")
		}

		const pressPercent = () => {
			store.dispatch("pressPercent")
		}

		return {
			updateCurrentOperator,
			display,
			pressNumber,
			pressEqual,
			pressReset,
			pressDot,
			invertNumber,
			pressPercent
		}
	},
}
</script>

<style src="./TheCalculator.css" scoped>
</style>