import { createStore } from "vuex"

const maxFl = (a, b) => {
  let dotAIndex = a.indexOf(".")
  let aFl = dotAIndex > 0 ? a.substring(dotAIndex + 1).length : 0
  let dotBIndex = b.indexOf(".")
  let bFl = dotBIndex > 0 ? b.substring(dotBIndex + 1).length : 0
  return aFl > bFl ? aFl : bFl
}

function add(a, b) {
  let max = maxFl(a, b)
  let m = Math.pow(10, max)
  return (a * m + b * m) / m + ""
}

function subtract(a, b) {
  let max = maxFl(a, b)
  let m = Math.pow(10, max)
  return (a * m - b * m) / m + ""
}

function multiply(a, b) {
  let max = maxFl(a, b)
  let m = Math.pow(10, max)
  return (a * m * b * m) / m + ""
}

function divide(a, b) {
  let max = maxFl(a, b)
  let m = Math.pow(10, max)
  return (a * m) / (b * m) + ""
}

const store = createStore({
  state: {
    currentOperator: "",
    leftEquation: "0",
    rightEquation: "",
    lastResult: "",
  },
  getters: {
    display: (state) => {
      return state.leftEquation + state.currentOperator + state.rightEquation
    },
  },
  mutations: {
    setCurrentOperator(state, operator) {
      state.currentOperator = operator
    },
    clearCurrentOperator(state) {
      state.currentOperator = ""
    },
    resetDisplay(state) {
      state.display = "0"
    },
    addToLeftEquation(state, number) {
      if (!number && state.leftEquation === "0") return
      if (state.leftEquation === "0") state.leftEquation = ""
      state.leftEquation += number
    },
    addToRightEquation(state, number) {
      if (!number && state.rightEquation === "0") return
      if (state.rightEquation === "0") state.rightEquation = ""
      state.rightEquation += number
    },
    resetLeftEquation(state) {
      state.leftEquation = "0"
    },
    resetRightEquation(state) {
      state.rightEquation = ""
    },
    setLeftEquation(state, equation) {
      state.leftEquation = equation
    },
  },
  actions: {
    updateCurrentOperator({ commit, state, dispatch }, operator) {
      if (state.currentOperator) {
        if (state.leftEquation && state.rightEquation) {
          dispatch("pressEqual")
        }
      }
      commit("setCurrentOperator", operator)
    },
    pressNumber({ commit, state }, number) {
      if (state.lastResult && !state.currentOperator) {
        commit("resetLeftEquation")
        commit("addToLeftEquation", number)
        state.lastResult = ""
        return
      }
      if (!state.currentOperator) {
        return commit("addToLeftEquation", number)
      }
      return commit("addToRightEquation", number)
    },
    pressEqual({ commit, state }) {
      let result = state.leftEquation
      if (state.currentOperator) {
        const le = state.leftEquation
        const re = state.rightEquation
        switch (state.currentOperator) {
          case "+":
            result = add(le, re)
            break
          case "-":
            result = subtract(le, re)
            break
          case "×":
            result = multiply(le, re)
            break
          case "/":
            result = divide(le, re)
            break
        }
      }

      state.lastResult = result
      commit("setLeftEquation", result)
      commit("clearCurrentOperator")
      commit("resetRightEquation")
    },
    pressReset({ commit }) {
      commit("clearCurrentOperator")
      commit("resetLeftEquation")
      commit("resetRightEquation")
    },
    pressDot({ state, commit }) {
      if ((state.lastResult || state.leftEquation === "0") && !state.currentOperator) {
        commit("resetLeftEquation")
        commit("addToLeftEquation", 0 + ".")
        state.lastResult = ""
        return
      }
      if (state.currentOperator && state.rightEquation === "") {
        commit("resetRightEquation")
        commit("addToRightEquation", 0 + ".")
      }
      if (!state.currentOperator) {
        if (state.leftEquation[state.leftEquation.length - 1] === ".") return
        return commit("addToLeftEquation", ".")
      }
      if (state.rightEquation[state.rightEquation.length - 1] === ".") return
      return commit("addToRightEquation", ".")
    },
    invertNumber({ state }) {
      if (state.rightEquation) {
        return (state.rightEquation = state.rightEquation.includes("-")
          ? state.rightEquation.substring(1)
          : "-" + state.rightEquation)
      }

      return (state.leftEquation = state.leftEquation.includes("-")
        ? state.leftEquation.substring(1)
        : "-" + state.leftEquation)
    },
    pressPercent({state}) {
      if(state.rightEquation) {
        return state.rightEquation = "" + state.rightEquation / 100
      }
      return state.leftEquation = "" + state.leftEquation / 100
    }
  },
  modules: {},
})

export default store

export function useStore() {
  return store
}
